<template>
    <div class="check-code-history-root">
        <div class="check-code-history-block">
            <div class="check-code-history-line">
                <span class="check-code-history-left">工业标识</span>
                <span class="check-code-history-right"
                    ><b>{{ code.idisCode }}</b></span
                >
            </div>
            <div class="check-code-history-line">
                <span class="check-code-history-left">对象名称</span>
                <span class="check-code-history-right"
                    ><b>{{ code.productName }}</b></span
                >
            </div>
        </div>
        <div v-for="his in history" :key="his.id" class="check-code-history-block">
            <div class="check-code-history-line">
                <span class="check-code-history-left">扫码时间</span>
                <span class="check-code-history-right">{{ his.createTime }}</span>
            </div>
            <div class="check-code-history-line">
                <span class="check-code-history-left">扫码地点</span>
                <span class="check-code-history-right">{{ his.accessAddress }}</span>
            </div>
        </div>
    </div>
</template>

<script>
import { getJsonResult } from '@/utils/server-wrapper'

export default {
    data() {
        return {
            code: {},
            codeAttr: {},
            history: []
        }
    },
    async beforeRouteEnter(to, from, next) {
        const idisCode = encodeURIComponent(to.query.idisCode)
        const { code, codeAttr } = await getJsonResult(`/wx/codeInfo?idisCode=${idisCode}`)
        const history = await getJsonResult(`/wx/check-code-history?codeId=${code.id}&idisCode=${idisCode}`)
        next(vm => {
            vm.$data.code = code
            vm.$data.codeAttr = codeAttr
            vm.$data.history = history
        })
    }
}
</script>

